<template>
    <el-form ref="form" :model="formModel" :inline="inline" label-width="80px">
        <el-form-item v-for="item in formItem" :label="item.label" :key="item.label">
            <el-input v-if="item.type==='input'" v-model="formModel[item.model]" :placeholder="'请输入'+(item.label||'')" @change="lossBlur"></el-input>
            <el-select v-else-if="item.type==='select'" v-model="formModel[item.model]" :placeholder="'请选择'+(item.label||'')">
                <el-option v-for="subItem in item.opts" :label="subItem.label" :value="subItem.val" :key="subItem.val"></el-option>
            </el-select>
            <el-date-picker v-else-if="item.type==='date'" type="date" value-format="yyyy-MM-dd" :placeholder="'选择'+(item.label||'')" v-model="formModel[item.model]" style="width: 100%;"></el-date-picker>
            <el-switch v-else-if="item.type==='switch'" v-model="formModel[item.model]"></el-switch>
            <el-checkbox-group v-else-if="item.type==='checkbox'" v-model="formModel[item.model]">
                <el-checkbox v-for="subItem in item.group" :label="subItem" name="type" :key="subItem"></el-checkbox>
            </el-checkbox-group>
            <el-radio-group v-else-if="item.type==='radio'" v-model="formModel[item.model]">
                <el-radio v-for="subItem in item.group" :label="subItem" :key="subItem"></el-radio>
            </el-radio-group>
            <el-input v-else-if="item.type==='textarea'" type="textarea" v-model="formModel[item.model]"></el-input>
        </el-form-item>
        <el-form-item><slot name="footer"></slot></el-form-item>
    </el-form>
</template>

<script>
export default {
    name: 'MyForm',
    props: {
        formItem: Array,
        formModel: Object,
        inline: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        lossBlur() {
            this.$emit('changeBlur')
        }
    },
}
</script>

