<template>
    <el-table :data="data" stripe style="width: 100%" v-loading="loading">
        <el-table-column v-for="(value,key) in label" :prop="key" :label="value" :key="key"></el-table-column>
        <el-table-column label="操作" v-if="operate">
            <template slot-scope="scope">
                <el-button size="mini" @click="edit_user(scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="del_user(scope.row.id)">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
export default {
    name: 'MyTable',
    props: {
        loading: Boolean,
        data: Array,
        label: Object,
        operate: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        edit_user(row) {
            this.$emit('editUser', row)
        },
        del_user(index) {
            this.$emit('delUser', index)
        }
    },
}
</script>
