<template>
    <div style="height:100%">
        <el-dialog :visible.sync="dialogVisible" width="30%">
            <my-form :formItem="add_formData" :formModel="formModel" >
                <template slot="footer" class="dialog_footer">
                    <el-col :span="11" style="text-align: center"><el-button @click="dialogVisible=false">取消</el-button></el-col>
                    <el-col :span="11" style="text-align: center"><el-button type="primary" @click="confirm">确定</el-button></el-col>                
                </template>
            </my-form>                         
        </el-dialog>
        <div class="operate">
            <div>
               <el-button type="primary" @click="add_user">新增</el-button> 
            </div>           
            <div>
                <my-form :formItem="search_form" :formModel="search_model" :inline="true" @changeBlur="lossInput">
                    <el-button slot="footer" type="promary" @click="lossInput">搜索</el-button>
                </my-form>
            </div>
        </div>
        <my-table class="userTable" 
            :loading="loading"
            :data="pageTableData" 
            :label="tableLabel" 
            operate 
            @editUser="edit_user" 
            @delUser="del_user"
        ></my-table>
        <div class="tablePage">
            <el-pagination
            :page-size="page.pageSize" 
            :pager-count="5" 
            layout="prev, pager, next" 
            :total="tableData.length"
            :hide-on-single-page="true"
            @current-change="changePage"
            ></el-pagination>
        </div>   
    </div>
</template>

<script>
import MyForm from '../../components/form'
import MyTable from '../../components/table.vue'
import {getUserData,addUserData,editUserData,delUserData,searchKeyword} from '../../api/data'
export default {
    name: "user",
    components: {
        MyForm,
        MyTable
    },
    data() {
        return {
            dialogVisible: false,
            loading: false,
            tableData: [],
            pageTableData: [],
            page: {
                pageSize: 10,
                currentPage: 1
            },
            tableLabel: {
                name: '姓名',
                age: '年龄',
                sex: '性别',
                birth: '生日',
                addr: '地址'
            },
            add_formData: [
                {
                   model: 'name',
                   label: '姓名',
                   type: 'input' 
                },{
                   model: 'age',
                   label: '年龄',
                   type: 'input' 
                },{
                   model: 'sex',
                   label: '性别',
                   type: 'radio',
                   group: ['男', '女'] 
                },{
                   model: 'birth',
                   label: '出生日期',
                   type: 'date' 
                },{
                   model: 'addr',
                   label: '地址',
                   type: 'input' 
                },
            ],
            formModel: {
                name: '',
                age: '',
                sex: '',
                birth: '',
                addr: ''
            },
            search_form: [{
                type: 'input',
                model: 'search_keyword'
            }],
            search_model: {
                search_keyword: ""
            },
            operate_type: ""
        }
    },
    created() {
        this.init()
    },
    methods: {
        init() {
            this.loading = true
            getUserData().then(res => {
                this.tableData = res.data
                this.loading = false
                this.changePage(this.page.currentPage)
            })
        }, 
        add_user() {
            this.dialogVisible = true
            this.operate_type = "addUser"
            this.formModel = {
                name: '',
                age: '',
                sex: '',
                birth: '',
                addr: '',
                id: ''
            }

        },
        edit_user(row) {
            this.dialogVisible = true
            this.operate_type = "editUser"
            this.formModel = row
        },
        del_user(user_id) {
            this.$confirm('此操作将永久删除该用户数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    delUserData(user_id).then(res => {
                    this.tableData = res.data
                    this.init()
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
        },
        confirm() {
            if(this.operate_type === "addUser") {
                if(this.formModel.name != '') {
                    addUserData(this.formModel).then(res => {
                        this.dialogVisible = false
                        if(res.data){
                            this.$message({
                                message: '已成功添加',
                                type: 'success'
                            });
                            this.init()
                        }
                    })
                }
                else{
                    this.dialogVisible = false
                    this.$message({
                        message: '姓名不能为空',
                        type: 'warning'
                    })
                }
            }
            else if(this.operate_type === "editUser") {
                editUserData(this.formModel).then(() => {
                    this.dialogVisible = false
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    })
                    this.init()
                })
            }
        },
        changePage(page) {
            this.pageTableData = this.tableData.slice(Math.imul(page-1,this.page.pageSize), Math.imul(page,this.page.pageSize))
        },
        lossInput() {
            if(this.search_model.search_keyword!='') {
                searchKeyword({
                    keyword: this.search_model.search_keyword
                }).then((res) => {
                    this.tableData = res.data
                    this.changePage(1)
                })
            }  
            else{
                this.init()
            } 
        }
    },
}
</script>

<style lang="scss" scoped>
.operate{
    display: flex;
    justify-content: space-between;  
}
.userTable{
    height: calc(100% - 100px);
    position: relative;
    overflow: auto;  
    &::-webkit-scrollbar{
        display: none;
    }
}
.tablePage{
    width: 100%;
    position: relative;
    .el-pagination{
        position: absolute;
        right: 0;
    }
}
</style>